import React from "react";
import { Link } from "react-router-dom";

import electronics from "../../images/electronics.jpg";
import jewelery from "../../images/jewelery.jpg";
import mensclothing from "../../images/mensclothing.jpg";
import womensclothing from "../../images/womensclothing.jpg";
import luxus from "../../images/luksus.jpg";
import elegant from "../../images/elegant.jpg";

import "bootstrap/dist/css/bootstrap.min.css";
import Header from "../Header/Header";

const Home = () => {
  window.scrollTo(0, 0);

  return (
    <>
      <Header />
      <div className="container">
        <div className="row">
          <div className="col-md-4 d-flex justify-content-center">
            <img src={elegant} alt="" style={{ maxWidth: "100%" }} />
          </div>
          <div className="col-md-8 bg-light d-flex flex-column justify-content-center align-items-center p-3">
            <h3 className="px-3 ">
              MainShop to miejsce gdzie znajdziesz dla siebie{" "}
              <span className="text-success"> najlepszej jakości </span>
              produkty, które uzupełniają twój wizerunek.
            </h3>
            <p className="px-3">
              Wybierając nasze produkty masz pewność, że korzystasz z
              najbardziej zaufanego portalu w roku 2020. Chcesz nas lepiej
              poznac ? Zapraszamy
            </p>
          </div>
          <div className="col-md-10 d-flex flex-column justify-content-center align-items-center p-3 mx-auto mt-3">
            <blockquote class="blockquote text-center">
              <p class="mb-2 display-4">
                „Nie potrafimy podać niezawodnego przepisu na sukces, ale możemy
                podać przepis na porażkę: staraj się wszystkich zadowolić”.
              </p>
              <footer class="blockquote-footer">
                CEO <cite title="Source Title"> of MainShop</cite>
              </footer>
            </blockquote>
            <div className="row d-flex justify-content-center align-items-center p-3">
              <div className="col-md-5">
                <img src={luxus} alt="" />
              </div>
              <div className="col-md-5">
                <p>
                  To motto jest filarem naszej filozofii. Dlatego właśnie nasze
                  produkty zaprojektowane są by spełnić oczekiwania najbardziej
                  wymagających klientów.
                </p>
              </div>
            </div>
          </div>
          <div className="col-md-10 mx-auto row">
            <p className="text-center m-4">
              Przez ponad 10 lat działalności zaufało nam już tysiące klientów.
              W bogatym asortymencie każdy znajdzie coś dla siebie i może zadbać
              o swój wizerunek już dziś.{" "}
              <strong>Przekonaj sie o tym sam !</strong>
            </p>
            <div className="col-md-3">
              <img src={mensclothing} alt="" className="w-100" />
              <p className="text-muted text-center">Moda męska</p>
            </div>
            <div className="col-md-3">
              <img src={womensclothing} alt="" className="w-100" />
              <p className="text-muted text-center">Moda żeńska</p>
            </div>
            <div className="col-md-3">
              <img src={jewelery} alt="" className="w-100" />
              <p className="text-muted text-center">Biżuteria</p>
            </div>
            <div className="col-md-3">
              <img src={electronics} alt="" className="w-100" />
              <p className="text-muted text-center">Elektronika</p>
            </div>
          </div>
          <div className="col-md-10 d-flex flex-column justify-content-center mx-auto my-5">
            <Link to="/shop" className="btn btn-outline-success my-2 my-sm-0">
              Zapraszamy do sklepu
            </Link>
          </div>
        </div>
      </div>
    </>
  );
};

export default Home;
